body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Raleway';
  src: local('Raleway'), url(/react-portfolio-2020/static/media/Raleway-Regular.84abe14c.ttf) format('truetype');
}

@font-face {
  font-family: 'Raleway-SemiBold';
  src: local('Raleway-SemiBold'), url(/react-portfolio-2020/static/media/Raleway-SemiBold.6ec58d1e.ttf) format('truetype');
}


:root {
  --off-white: #fafafa;
}

.background{
  width:100%;
  height:100vh;
  background:url(/react-portfolio-2020/static/media/webportfolio-banner.cecff9ae.png) no-repeat center center;
  background-size:cover;
  overflow: visible;
  margin-top: -90px;
}

.nav-top {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 90px;
  z-index: 1000;
  overflow-x: hidden;
}

.nav-tpp > body {
  background-color: var(--off-white);
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 60px;
  width: 60px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav-top .nav-content {
  max-width: 100%;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  background-color: transparent;
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  padding-left: 15px;
  padding-right: 15px;
  color: #fafafa;
  font-family: Raleway-SemiBold;
  font-size: 20px;

  letter-spacing: 0.07px;
  text-align: center;
  text-shadow: 0 2px 4px #000000;

}

.nav-item:hover {
  /*color: black;*/
  cursor: pointer;
}

.nav-header{
  font-size: 40px;
  letter-spacing: 0.23px;
  text-align: center;
  color: #fafafa;
}

